$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Parallax Section
$scroll-factor : 1;

.wp-block-vbb-video-bg {
	position: relative;
	overflow: hidden;

	* {
		box-sizing: border-box;
	}

	.vbbVideoPlayer {
		min-width: 100%;
		min-height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 0;
	}

	.vbbVideoOverlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: #4527a49e;
		z-index: 5;
	}

	.vbbVideoContent {
		width: 100%;
		height: 100%;
		max-height: 100%;
		z-index: 10;
		color: #fff;
		display: flex;
		flex-direction: column;

		* {
			z-index: 10;
		}

		p {
			max-width: 100%;
		}

		.block-editor-block-list__layout {
			>* {
				max-width: 100%;
			}
		}
	}
}